.app {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  flex-direction: column;
}

.ceiling, .floor {
  height: 25%;
  display: flex;
}
.floor {
  align-items: flex-end;
}
.ceiling .piece, .floor .piece {
  background-color: #8a0303;
  opacity: 0.8;
  /* background-image: radial-gradient(circle at center center, red, #8a0303), repeating-radial-gradient(circle at center center, #444cf7, #444cf7, 10px, transparent 20px, transparent 10px); */
  /* background-blend-mode: multiply; */
}

.center {
  flex: 1;
  position: relative;
}

.me {
  position: absolute;
  background-color: transparent;
}

.me img {
  position: absolute;
  top: -30%;
  left: -12%;
  width: 140%;
  height: 140%;
}

.asteroid {
  background-color: transparent;
  position: absolute;
}

.asteroid img {
  position: absolute;
  top: -25%;
  left: -25%;
  width: 140%;
  height: 140%;
}

/* .asteroid:after {
  position: absolute;
  content: '';
  background-image: url(./darth-siena.png);;
} */

.you-died, .next-clue, .start-prompt {
  position: absolute;
  top: 10%;
  height: 200px;
  width: 200px;
  left: calc(50% - 105px);
  box-shadow: 0 3px 10px rgb(0 0 0);
  background-color: grey;
  z-index: 10;
  padding: 10px;
  text-align: center;
  transition: opacity 1000ms ease-in;
}
.start-prompt {
  background-color: coral;
  color: white;
}
.next-clue {
  bottom: 10%;
  top: unset;
  background-color: #ccff15;
  box-shadow: 0 3px 10px gold;
}

.clock {
  position: absolute;
  top: 0;
  left: calc(50% - 20px);
  width: 40px;
  background-color: black;
  color: white;
  text-align: center;
}

button {
  display: block;
  margin: auto;
  margin-top: 10px;
  margin-bottom: 10px;
}

input {
  margin-top: 10px;
}